import React from "react";
import logo from "./images/zonasys_icon.png";
function Main() {
  return (
    <div>
        <section id="main" className="bg-success p-5 mt-3 text-center text-light">

        <img src={logo} alt="" className="mb-4" />
        <div className="container bg-light text-dark mt-3 p-5" style={{borderRadius:20,backgroundColor:'#fff opacity(0.5)'}}>
            <h1>Unveil its "Goal"</h1>
            <p>A Vision For Business Growth</p>
  <p>The company aims to increase more market share for domestic and overseas countries by expand domestic and overseas branches office, with the ultimate goal of becoming a leader in target businesses.</p>            

            <button className="btn btn-outline-light">Get Started</button>
        </div>
        </section>
    </div>
  );
}

export default Main;
