import React from 'react'
import ReactDOM from 'react-dom'
import "./App.css"

function Service() {
   
    return (

        
        <div className="container-fluid  text-light text-center p-10 service-section">
            <div className="container mt-10 mb-10 p-5">
                <div className="container bg-service">
                <h1 className="mt-15">Services</h1>
                <h3>A Vision For Business Growth</h3>
                <p>The company has closely monitored the sustained growth of the overall economic situation during the last 10 years, emphasising the importance of shaping corporate growth objectives in step with these general economic expansion trend. The company aims to increase more market share for domestic and overseas countries by expand domestic and overseas branches office, with the ultimate goal of becoming a leader in target businesses.</p>
                <ul className="p-5">
                    <li className="btn btn-dark mr-4 mb-4">Hotel Management System</li>
                    <li className="btn btn-dark mr-4 mb-4">Point of sale System</li>
                    <li className="btn btn-dark mr-4 mb-4">Accounting System</li>
                    <li className="btn btn-dark mr-4 mb-4">Hr system</li>
                </ul>
                </div>
                </div>
        </div>
    )
}

export default Service
